.segments {
  @include section;
  &__title {
    padding-top: 4px;
    @include h2;
    margin-bottom: 20px;
    @media (max-width: 1600px) {
      padding-top: 8px;
    }
    @media (max-width: 1279px) {
      padding-top: 4px;
    }
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__item {
    min-height: 574px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 24px;
    border-radius: 4px;
    position: relative;
    z-index: 1;
    overflow: hidden;
    transition: all $transition;
    @media (max-width: 1600px) {
      min-height: 450px;
      padding: 16px;
    }
    @media (max-width: 767px) {
      min-height: 300px;
    }
    &:hover {
      @include shadow;
      &:after {
        opacity: 1;
      }
      .segments__item-arrow {
        border-color: $white;
      }
    }
    &:active {
      &::after {
        border-color: $green-active;
      }
    }
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(#164A31, 0) 57%, rgba(#164A31, 1) 100%);
      z-index: -1;
    }
    &::after {
      content: '';
      position: absolute;
      inset: 0;
      border-radius: inherit;
      border: 2px solid $green;
      pointer-events: none;
      opacity: 0;
      transition: all $transition;
    }
    &-img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -2;
      img {
        @include cover;
      }
    }
    &-content {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: $gap;
      color: $white;
    }
    &-title {
      @include h3;
      margin-bottom: 8px;
      @media (max-width: 1279px) {
        font-size: 23px;
      }
      @media (max-width: 767px) {
        font-size: 23px;
      }
    }
    &-description {
      @include h4;
      @media (max-width: 767px) {
        font-size: 17px;
      }
    }
    &-arrow {
      width: 46px;
      height: 46px;
      flex-shrink: 0;
      border: 1px solid transparent;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: all $transition;
      svg {
        width: 24px;
        height: 24px;
        stroke: $white;
        stroke-width: 1.6;
      }
    }
  }
  &__banner {
    display: flex;
    flex-direction: column;
    padding: 30px 24px 24px;
    background-color: $green;
    border-radius: 4px;
    color: $white;
    position: relative;
    z-index: 1;
    overflow: hidden;
    @media (max-width: 1600px) {
      padding: 18px 16px 16px;
    }
    &-title {
      @include h5;
      margin-bottom: 13px;
      @media (max-width: 1279px) {
        font-size: 22px;
      }
      @media (max-width: 767px) {
        font-size: 22px;
      }
    }
    &-description {
      @include body-medium;
      margin-bottom: 12px;
      @media (max-width: 767px) {
        margin-bottom: 39px;
      }
    }
    &-img {
      max-width: 385px;
      margin: auto auto 12px;
      position: relative;
      z-index: -2;
      @media (max-width: 1600px) {
        max-width: 268px;
        margin: auto auto 24px;
      }
      img {
        @include contain;
      }
    }
    &-button {
      width: 100%;
    }
    &-gradient {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba($green, 0) 56%, rgba($green, 1) 100%);
      z-index: -1;
      &::before, &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(180deg, rgba($green, 0) 56%, rgba($green, 1) 100%);
      }
    }
  }
}