.news-all {
  @include section;
  &__heading {
    padding-top: 4px;
    margin-bottom: 24px;
  }
  &__title {
    @include h2;
    margin-bottom: 19px;
  }
  &__filter {
    width: max-content;
    border-top: 1px solid $base-300;
    @media (max-width: 767px) {
      width: auto;
      margin: 0 -16px;
    }
    &-inner {
      display: flex;
      align-items: center;
      gap: 41px;
      margin-top: -1px;
      @media (max-width: 767px) {
        overflow-x: auto;
        padding: 0 16px;
        -ms-overflow-style: none;
        scrollbar-width: none;
        &::-webkit-scrollbar {
          width: 0;
          height: 0;
        }
      }
    }
    &-item {
      min-height: 50px;
      display: flex;
      align-items: center;
      padding: 12px 0;
      @include h4;
      color: $base-700;
      position: relative;
      transition: all $transition;
      @media (max-width: 767px) {
        font-size: 17px;
      }
      &:not(.news-all__filter-item_active) {
        &:hover {
          color: $base-900;
        }
      }
      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: $green;
        opacity: 0;
        transition: all $transition;
      }
      &_active {
        color: $green;
        &::before {
          opacity: 1;
        }
      }
    }
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    margin-bottom: 24px;
    @media (max-width: 1600px) {
      grid-template-columns: repeat(3, 1fr);
    }
    @media (max-width: 1279px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}