.advantages {
  @include section;
  background-color: $white;
  &__content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__title {
    @include h5;
    @media (max-width: 1279px) {
      padding-top: 2px;
      margin-bottom: 20px;
    }
    @media (max-width: 767px) {
      font-size: 21px;
      margin-bottom: 22px;
    }
  }
  &__list {
    grid-column: span 3;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 50px $gap;
    counter-reset: num;
    @media (min-width: 1601px) {
      &.grid-2 {
        grid-template-columns: repeat(2, 1fr);
      }
    }
    @media (max-width: 1600px) {
      grid-template-columns: repeat(2, 1fr);
      gap: 50px;
    }
    @media (max-width: 1279px) {
      gap: $gap;
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
      gap: 12px;
    }
  }
  &__item {
    min-height: 120px;
    display: flex;
    align-items: center;
    @media (max-width: 767px) {
      min-height: 100px;
    }
    &-number {
      display: inline-flex;
      align-items: center;
      height: 62px;
      flex-shrink: 0;
      margin-right: 74px;
      @include h5;
      color: $base-700;
      position: relative;
      @media (max-width: 1919px) {
        font-size: 31px;
      }
      @media (max-width: 1600px) {
        margin-right: 54px;
      }
      @media (max-width: 1279px) {
        font-size: 31px;
        margin-right: 62px;
      }
      @media (max-width: 767px) {
        justify-content: flex-end;
        min-width: 40px;
        font-size: 22px;
        margin-right: 40px;
      }
      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: calc(100% - 2px);
        width: 20px;
        height: 100%;
        border-left: 1px solid $base-700;
        background-color: $white;
        transform: skewX(-30deg);
      }
      &::before {
        counter-increment: num;
        content: counter(num, decimal-leading-zero);
      }
    }
    &-title {
      @include h4;
    }
  }
}