.select2 {
  @include body;
  &.select2_white {
    ~ .select2-container {
      .select2-selection {
        background: $white !important;
      }
    }
  }
  &-block {
    width: 100%;
    max-width: 100%;
    // &_active {
    //   .select2-selection {
    //     background-color: $white !important;
    //     border-color: $green !important;
    //   }
    // }
  }
  &-search {
    &--dropdown {
      display: none !important;
    }
    &__field {
      font-size: inherit !important;
      margin: 0 !important;
    }
  }
  &-selection {
    height: 50px !important;
    background: $base-100 !important;
    border: 1px solid $base-100 !important;
    border-radius: 2px !important;
    transition: all $transition;
    &.select2-selection--multiple {
      &::after {
        content: '';
        position: absolute;
        top: 50% !important;
        right: 15px !important;
        transform: translateY(-50%);
        width: 16px !important;
        height: 16px !important;
        background: url(../img/content/arrow-dropdown.svg) 50% / contain no-repeat;
        transition: all $transition;
      }
    }
    &.is-selected {
      .select2-search {
        display: none !important;
      }
    }
    &--multiple {
      cursor: pointer !important;
    }
    &:hover {
      border-color: $base-300 !important;
    }
    &:focus-visible {
      outline-color: transparent;
    }
    &__placeholder {
      color: $base-900 !important;
    }
    &__clear {
      display: none;
    }
    &__rendered {
      max-width: calc(100% - 15px);
      padding: 0 15px !important;
      color: $base-900 !important;
      line-height: 48px !important;
      transition: all $transition;
    }
    &__display {
      display: flex;
      gap: 4px;
    }
    &__arrow {
      top: 50% !important;
      right: 15px !important;
      transform: translateY(-50%);
      width: 16px !important;
      height: 16px !important;
      background: url(../img/content/arrow-dropdown.svg) 50% / contain no-repeat;
      transition: all $transition;
      b {
        display: none !important;
      }
    }
  }
  &-container--open {
    .select2 {
      &-selection {
        background-color: $white !important;
        border-color: $green !important;
        &__arrow {
          transform: translateY(-50%) rotate(-180deg) !important;
        }
        &::after {
          transform: translateY(-50%) rotate(-180deg) !important;
        }
      }
    }
  }
  &-results__option {
    display: flex;
    min-height: 50px;
    padding: 14px !important;
    border: none !important;
    border-radius: 0 !important;
    @include body;
    color: $base-900 !important;
    transition: all $transition;
    &--highlighted {
      background: $base-100 !important;
    }
    &[aria-selected="true"] {
      background: $base-100 !important;
      color: $green !important;
      &::before {
        border-color: $green !important;
        background: $green url(../img/content/check.svg) 50% / 11px no-repeat;
      }
    }
  }
  &-dropdown {
    top: 2px;
    background-color: $white;
    border: 1px solid $base-300 !important;
    border-radius: 2px !important;
    &_multiple {
      .select2-results__option {
        position: relative;
        &::before {
          content: '';
          display: inline-block;
          width: 20px;
          height: 20px;
          flex-shrink: 0;
          border: 1px solid $base-700;
          border-radius: 2px;
          margin-right: 18px;
          transition: background-color $transition;
        }
      }
    }
    &_price {
      .select2-results__option {
        min-height: 0;
        padding: 8px !important;
      }
    }
  }
  // &-container--default .select2-results>.select2-results__options {
  //   max-height: 205px;
  //   &::-webkit-scrollbar {
  //     width: 1px;
  //   }
  //   &::-webkit-scrollbar-track {
  //     background: $light-green-gray;
  //     border-radius: 30px;
  //   }
  //   &::-webkit-scrollbar-thumb {
  //     width: 100%;
  //     background: $accent-dark;
  //     border-radius: 30px;
  //   }
  // }
}