.news-detail {
  @include section;
  background-color: $white;
  margin-bottom: 60px;
  @media (max-width: 1279px) {
    margin-bottom: 40px;
  }
  @media (max-width: 767px) {
    margin-bottom: 0;
  }
  &__body {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    align-items: flex-start;
    gap: $gap;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__side {
    grid-column: span 3;
    position: sticky;
    top: 90px;
    left: 0;
    transition: all $transition;
    @media (max-width: 1279px) {
      position: static;
      margin-bottom: 40px;
    }
    &-date {
      @include body-medium;
      color: $base-700;
      margin-bottom: 16px;
    }
    .tag {
      gap: 16px;
      margin-bottom: 16px;
    }
    &-title {
      padding-top: 15px;
      border-top: 1px solid $base-200;
      @include body-medium;
      color: $base-700;
      margin-bottom: 16px;
    }
    &-list {
      @media (max-width: 1279px) {
        max-width: 360px;
      }
      li {
        &:not(:last-child) {
          margin-bottom: 4px;
        }
      }
    }
    &-link {
      min-height: 37px;
      padding: 8px 0;
      display: inline-flex;
      align-items: center;
      gap: 12px;
      @include body-medium;
      transition: all $transition;
      svg {
        display: none;
        width: 16px;
        height: 16px;
        flex-shrink: 0;
        stroke: $green;
        transition: all $transition;
      }
      &_active {
        color: $green;
        svg {
          display: block;
        }
      }
    }
  }
  &__content {
    grid-column: 5 / span 7;
    display: flex;
    flex-direction: column;
    gap: 40px;
    @include body;
    @media (max-width: 1600px) {
      grid-column: 5 / span 8;
    }
    // @media (max-width: 767px) {
    //   font-size: 13px;
    //   line-height: 1.39;
    // }
    p {
      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
  }
  &__img {
    width: 100%;
    height: 600px;
    @media (max-width: 1600px) {
      height: 450px;
    }
    @media (max-width: 1279px) {
      margin-bottom: 40px;
    }
    img {
      @include cover;
    }
  }
  &__subtitle {
    @include h3;
    margin-bottom: 12px;
  }
  &__info {
    padding: 24px;
    background-color: $base-100;
    border-radius: 8px;
    @include h4;
    @media (max-width: 767px) {
      padding: 16px;
    }
    &-title {
      color: $green;
      margin-bottom: 8px;
      @media (max-width: 767px) {
        margin-bottom: 6px;
      }
    }
    .news-detail__description {
      @media (max-width: 767px) {
        font-size: 17px;
      }
    }
  }
}