.animate-text {
  span {
    display: inline;
    background: linear-gradient(to right, $base-700 50%, $base-900 50%) 0 0 / 200% 100%;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    box-decoration-break: slice;
    -webkit-box-decoration-break: slice;
  }
}