.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 49px;
  padding: 0 24px;
  background-color: $base-900;
  border-radius: 2px;
  transition: all $transition;
  @include button;
  color: $white;
  text-transform: uppercase;
  @media (max-width: 767px) {
    width: 100%;
  }
  &:hover {
    background-color: $gold;
    color: $white;
  }
  &:active {
    background-color: $gold-active;
  }
  &_white {
    background-color: $white;
    color: $base-900;
    &:hover {
      background-color: $green;
    }
    &:active {
      background-color: $green-active;
    }
  }
  &_green {
    background-color: $green;
    color: $white;
    &:hover {
      background-color: $green-active;
    }
    &:active {
      background-color: $base-900;
    }
  }
  &_hover_grey {
    &:hover {
      background-color: $base-200;
      color: $base-900;
    }
    &:active {
      background-color: $green-active;
      color: $white;
    }
  }
  &_border {
    border: 1px solid $base-300;
    background-color: $white;
    color: $base-900;
    &:hover {
      background-color: $green;
      border-color: $green;
    }
    &:active {
      background-color: $green-active;
      border-color: $green-active;
    }
  }
  &_disabled {
    pointer-events: none;
    background-color: $base-200;
    color: $base-700;
  }
}