.similar {
  @include section;
  position: relative;
  overflow: hidden;
  &__heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: $gap;
    margin-bottom: 24px;
  }
  &__title {
    @include h2;
  }
  &__slider {
    overflow: visible;
    &-item {
      height: auto;
    }
    .card {
      &::before {
        border-radius: inherit;
        border-bottom: 1px solid $base-300;
      }
      &:hover {
        border-radius: 2px;
      }
    }
  }
}