.services {
  @include section;
  background-color: $white;
  &__body {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      display: block;
      padding-bottom: 58px;
      position: relative;
    }
  }
  &__heading {
    padding-top: 4px;
    padding-right: 44px;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @media (max-width: 1600px) {
      padding-right: 0;
      margin-right: -28px;
    }
    @media (max-width: 1279px) {
      max-width: none;
      padding-right: 0;
      margin-right: 0;
      margin-bottom: 20px;
    }
    @media (max-width: 767px) {
      padding-top: 2px;
    }
  }
  &__title {
    @include h2;
  }
  &__button {
    position: sticky;
    bottom: 24px;
    width: 100%;
    margin-top: 82px;
    @media (max-width: 1279px) {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      width: auto;
      margin-top: 0;
    }
  }
  &__content {
    grid-column: span 3;
    margin-left: -44px;
    margin-bottom: -24px;
    @media (max-width: 1600px) {
      margin-left: 44px;
    }
    @media (max-width: 1279px) {
      margin: 0;
    }
  }
}