.awards {
  @include section;
  background-color: $white;
  @media (max-width: 1600px) {
    overflow: hidden;
  }
  &__body {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    @media (max-width: 1600px) {
      grid-template-columns: repeat(6, 1fr);
    }
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__title {
    max-width: 250px;
    @include h5;
    @media (max-width: 1279px) {
      max-width: none;
      padding-top: 3px;
      margin-bottom: 22px;
    }
  }
  &__content {
    grid-column: span 3;
    @media (max-width: 1600px) {
      grid-column: span 5;
      padding-left: 21px;
      margin-right: -40px;
    }
    @media (max-width: 1279px) {
      margin: 0;
      padding: 0;
    }
  }
  &__slider {
    @media (max-width: 1600px) {
      padding-right: 40px;
    }
    @media (max-width: 1279px) {
      padding-right: 0;
      overflow: visible;
    }
    &-item {
      height: auto;
      border-radius: 2px;
      overflow: hidden;
      &-img {
        width: 100%;
        height: 350px;
        img {
          @include cover;
        }
      }
      &-text {
        padding: 26px 16px;
      }
      &-title {
        @include h4;
        margin-bottom: 8px;
      }
      &-date {
        @include body-medium;
        color: $base-700;
      }
    }
  }
}