@mixin cover {
  height: 100%;
  object-fit: cover;
}

@mixin contain {
  height: 100%;
  object-fit: contain;
}

@mixin inter {
  font-family: 'Inter', sans-serif;
}

@mixin angst {
  font-family: 'Angst', sans-serif;
}

@mixin section {
  padding: 60px 0;
  @media (max-width: 1279px) {
    padding: 40px 0;
  }
}

@mixin h1 {
  font-size: 60px;
  @include angst;
  line-height: 1.1;
  @media (max-width: 1919px) {
    font-size: 40px;
  }
  @media (max-width: 1279px) {
    font-size: 34px;
  }
  @media (max-width: 767px) {
    font-size: 25px;
  }
}

@mixin h2 {
  font-size: 42px;
  @include angst;
  @media (max-width: 1919px) {
    font-size: 34px;
  }
  @media (max-width: 1279px) {
    font-size: 24px;
  }
  @media (max-width: 767px) {
    font-size: 21px;
  }
}

@mixin h3 {
  font-size: 29px;
  line-height: 1.5;
  letter-spacing: -.04em;
  @media (max-width: 1919px) {
    font-size: 23px;
  }
  @media (max-width: 1279px) {
    font-size: 20px;
  }
  @media (max-width: 767px) {
    font-size: 17px;
  }
}

@mixin h4 {
  font-size: 20px;
  line-height: 1.3;
  letter-spacing: -.04em;
  @media (max-width: 1919px) {
    font-size: 17px;
  }
  @media (max-width: 767px) {
    font-size: 15px;
  }
}

@mixin h5 {
  font-size: 31px;
  @include angst;
  @media (max-width: 1919px) {
    font-size: 22px;
  }
  @media (max-width: 1279px) {
    font-size: 21px;
  }
  @media (max-width: 767px) {
    font-size: 18px;
  }
}

@mixin body {
  font-size: 15px;
  line-height: 1.4;
  letter-spacing: -.02em;
  // @media (max-width: 767px) {
  //   font-size: 13px;
  // }
}

@mixin body-medium {
  font-size: 15px;
  font-weight: 500;
  line-height: 1.4;
  letter-spacing: -.02em;
  // @media (max-width: 767px) {
  //   font-size: 13px;
  // }
}

@mixin button {
  font-size: 14px;
  font-weight: 500;
  line-height: 1;
  letter-spacing: -.01em;
}

@mixin label {
  font-size: 13px;
  font-weight: 500;
  line-height: 1.23;
  letter-spacing: -.01em;
}

@mixin shadow {
  box-shadow: 2px 4px 32px rgba(#3F3F3F, .2);
}