.team {
  @include section;
  background-color: $white;
  @media (max-width: 1600px) {
    overflow: hidden;
  }
  &__body {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__title {
    @include h5;
    @media (max-width: 1279px) {
      padding-top: 3px;
      margin-bottom: 22px;
    }
  }
  &__content {
    grid-column: span 3;
    @media (max-width: 1600px) {
      margin-right: -40px;
    }
    @media (max-width: 1279px) {
      margin: 0;
    }
  }
  &__slider {
    @media (max-width: 1600px) {
      padding-right: 40px;
    }
    @media (max-width: 1279px) {
      padding-right: 0;
      overflow: visible;
    }
    &-item {
      height: auto;
      background-color: $base-100;
      &-img {
        width: 100%;
        height: 350px;
        img {
          @include cover;
        }
      }
      &-text {
        padding: 22px 16px 26px;
        @media (max-width: 767px) {
          padding-bottom: 30px;
        }
      }
      &-link {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: $gap;
        @include label;
        color: $base-700;
        margin-bottom: 4px;
        @media (max-width: 1279px) {
          margin-bottom: 6px;
        }
        @media (max-width: 767px) {
          margin-bottom: 4px;
        }
        svg {
          width: 24px;
          height: 24px;
          flex-shrink: 0;
          stroke: $base-900;
          stroke-width: 1.6;
        }
      }
      &-name {
        @include h4;
        margin-bottom: 8px;
        @media (max-width: 1600px) {
          font-size: 20px;
        }
        @media (max-width: 1279px) {
          font-size: 17px;
          margin-bottom: 10px;
        }
        @media (max-width: 767px) {
          font-size: 17px;
          margin-bottom: 8px;
        }
      }
      &-position {
        @include body-medium;
        color: $base-700;
      }
    }
  }
}