.peculiarities {
  padding: 35px 0 92px;
  background-color: $white;
  @media (max-width: 1600px) {
    padding-bottom: 60px;
  }
  @media (max-width: 1279px) {
    padding-bottom: 40px;
  }
  &__body {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__content {
    grid-column: 4 / span 8;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 64px;
    padding-right: 26px;
    @media (max-width: 1600px) {
      grid-column: 5 / span 7;
      grid-template-columns: repeat(2, 1fr);
      padding-right: 12px;
    }
    @media (max-width: 1279px) {
      gap: $gap;
      padding-right: 0;
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__item {
    @media (max-width: 1279px) {
      min-height: 150px;
    }
    @media (max-width: 767px) {
      min-height: auto;
    }
    &-img {
      width: 56px;
      height: 56px;
      margin-bottom: 16px;
      @media (max-width: 767px) {
        width: 32px;
        height: 32px;
      }
      svg {
        width: 100%;
        height: 100%;
      }
    }
    &-title {
      @include h4;
      @media (max-width: 1600px) {
        font-size: 20px;
      }
    }
  }
}