.service-card {
  display: grid;
  grid-template-columns: 356px auto;
  align-content: flex-start;
  gap: 32px 64px;
  padding: 24px 0;
  position: relative;
  @media (max-width: 1600px) {
    gap: $gap 32px;
  }
  @media (max-width: 1279px) {
    grid-template-columns: 302px auto;
  }
  @media (max-width: 767px) {
    display: block;
  }
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: $base-300;
  }
  &__img {
    grid-row: span 2;
    width: 100%;
    height: 242px;
    flex-shrink: 0;
    @media (max-width: 1279px) {
      grid-row: span 1;
    }
    @media (max-width: 767px) {
      display: block;
      height: 180px;
      margin-bottom: 8px;
    }
    img {
      @include cover;
    }
  }
  &__text {
    width: auto;
    flex-grow: 1;
    align-self: flex-start;
    @media (max-width: 767px) {
      width: 100%;
      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
  }
  &__heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: $gap;
    margin-bottom: 23px;
    @media (max-width: 1600px) {
      margin-bottom: 12px;
    }
    @media (max-width: 1279px) {
      margin-bottom: 32px;
    }
    @media (max-width: 767px) {
      gap: 8px;
      margin-bottom: 13px;
    }
    &:hover {
      .service-card {
        &__title {
          color: $green;
        }
        &__arrow {
          background-color: $base-200;
          svg {
            stroke: $green;
          }
        }
      }
    }
    &:active {
      .service-card {
        &__title {
          color: $green-active;
        }
        &__arrow {
          svg {
            stroke: $green-active;
          }
        }
      }
    }
  }
  &__title {
    max-width: 920px;
    @include h5;
    transition: all $transition;
  }
  &__arrow {
    align-self: flex-start;
    width: 46px;
    height: 46px;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $base-100;
    transition: all $transition;
    @media (max-width: 1279px) {
      width: 24px;
      height: 24px;
    }
    svg {
      width: 24px;
      height: 24px;
      stroke: $base-900;
      stroke-width: 1.6;
      transition: all $transition;
      @media (max-width: 1279px) {
        width: 16px;
        height: 16px;
      }
    }
  }
  &__description {
    max-width: 700px;
    @include body;
    color: $base-700;
    @media (max-width: 767px) {
      font-size: 13px;
    }
  }
  &__label {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px 15px;
    background-color: $base-100;
    border: 1px solid $base-100;
    border-radius: 2px;
    @include body;
    transition: all $transition;
    @media (max-width: 767px) {
      width: 100%;
      justify-content: space-between;
      font-size: 13px;
    }
    &s {
      grid-column-start: 2;
      align-self: flex-end;
      display: flex;
      flex-wrap: wrap;
      gap: 8px;
      @media (max-width: 1279px) {
        grid-column: span 2;
      }
    }
    &:hover {
      background-color: $white;
      border-color: $green;
    }
    &:active {
      background-color: $base-200;
      border-color: $base-200;
    }
    svg {
      width: 16px;
      height: 16px;
      stroke: $green;
      stroke-width: 1.6;
    }
  }
}