.form {
  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 16px;
    margin-bottom: 20px;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__privacy {
    @include label;
    color: $base-700;
  }
}

input, textarea {
  width: 100%;
  height: 50px;
  border: 1px solid $base-100;
  border-radius: 2px;
  background-color: $base-100;
  padding: 15px;
  appearance: none;
  @include body;
  color: $base-900;
  transition: all $transition;
  outline: none;
  cursor: pointer;
  &:hover {
    border-color: $base-300;
  }
  &:focus, &:active {
    border-color: $green;
  }
  &:not(:placeholder-shown):valid {
    border-color: $green;
  }
  &::placeholder {
    color: $base-700;
  }
  &:checked {
    ~ .label {
      &__box {
        background-color: $green;
        border-color: $green;
        &::after {
          opacity: 1;
        }
      }
    }
  }
  &.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}

