.label {
  display: flex;
  cursor: pointer;
  position: relative;
  z-index: 1;
  &s {
    display: flex;
    gap: 4px;
    height: 50px;
    padding: 4px;
    background-color: $base-100;
    border-radius: 2px;
    .label {
      flex: 1;
    }
  }
  &_checkbox {
    gap: 18px;
  }
  &_tag {
    input {
      &:checked {
        ~ .label__text {
          background-color: $white;
          border-color: $green;
        }
      }
    }
    .label__text {
      display: flex;
      padding: 6px 15px 8px;
      background-color: $base-100;
      border-radius: 2px;
      border: 1px solid $base-100;
      @include label;
      &:hover {
        background-color: $base-200;
        border-color: $base-200;
      }
    }
  }
  &_tab {
    input {
      &:checked {
        ~ .label__text {
          background-color: $green;
          color: $white;
        }
      }
    }
    .label__text {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0 8px;
      background-color: $base-100;
      border-radius: 2px;
      &:hover {
        background-color: $base-200;
      }
    }
  }
  &__box {
    // margin-top: 4px;
    width: 20px;
    height: 20px;
    flex-shrink: 0;
    border: 1px solid $base-700;
    border-radius: 2px;
    overflow: hidden;
    position: relative;
    transition: all $transition;
    // &::after {
    //   content: '';
    //   position: absolute;
    //   inset: 2px;
    //   background-color: $accent-dark;
    //   border-radius: 1px;
    //   opacity: 0;
    //   transition: all $transition;
    // }
  }
  &__text {
    @include body;
    transition: all $transition;
  }
}