.slider {
  &-button {
    &s {
      display: flex;
      align-items: center;
      gap: 9px;
      @media (max-width: 1279px) {
        display: none;
      }
      .slider {
        &-button {
          &-prev, &-next {
            position: static;
            transform: none;
          }
        }
      }
    }
    &-prev, &-next {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 46px;
      height: 46px;
      background-color: $white;
      transform: translateY(-50%);
      outline: none;
      margin: 0;
      z-index: 10;
      transition: all $transition;
      &.swiper-button-disabled {
        opacity: 1;
        svg {
          stroke: $base-300;
        }
      }
      &.swiper-button-lock {
        display: none;
      }
      &:hover {
        background-color: $base-200;
        svg {
          stroke: $green;
        }
      }
      &:active {
        background-color: $base-300;
        svg {
          stroke: $green-active;
        }
      }
      &::after {
        content: none;
      }
      svg {
        width: 24px;
        height: 24px;
        stroke: $base-900;
        stroke-width: 1.6;
        transition: all $transition;
      }
    }
    &-prev {
      left: 0;
    }
    &-next {
      right: 0;
    }
  }
  &-pagination.swiper-pagination {
    top: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    gap: 8px;
    padding: 0 16px;
    @media (max-width: 1279px) {
      top: auto;
      bottom: 16px;
    }
    @media (max-width: 767px) {
      padding: 0 8px;
    }
    &.swiper-pagination-lock {
      display: none;
    }
    .swiper-pagination-bullet {
      flex: 1;
      width: 100%;
      height: 100%;
      background: none;
      border-radius: 0;
      margin: 0;
      opacity: 1;
      position: relative;
      transition: all $transition;
      @media (max-width: 1279px) {
        height: 2px;
      }
      &::before {
        content: '';
        position: absolute;
        bottom: 12px;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: $base-300;
        transition: all $transition;
        @media (max-width: 1279px) {
          bottom: 0;
          height: 100%;
        }
      }
      &-active {
        &::before {
          background-color: $white;
        }
      }
    }
  }
}