.card-small {
  display: flex;
  background-color: $white;
  position: relative;
  z-index: 1;
  border-radius: 2px;
  overflow: hidden;
  flex-shrink: 0;
  &.active {
    &::before {
      opacity: 1;
    }
  }
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 1px solid $green;
    pointer-events: none;
    z-index: -1;
    opacity: 0;
    transition: all $transition;
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 230px;
    height: 100%;
    z-index: -2;
    img {
      @include cover;
    }
  }
  &__text {
    width: calc(100% - 230px);
    margin-left: auto;
    padding: 16px;
    .tag {
      margin-bottom: 16px;
    }
  }
  &__title {
    @include h4;
    margin-bottom: 12px;
    @media (max-width: 1600px) {
      font-size: 20px;
    }
  }
  &__id {
    @include label;
    color: $base-700;
    margin-bottom: 10px;
  }
  &__address {
    @include body;
  }
}