.pagination {
  display: inline-flex;
  width: auto;
  gap: 6px;
  padding: 4px;
  border-radius: 4px;
  background-color: $base-100;
  @media (max-width: 767px) {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    padding: 4px 0;
  }
  @media (max-width: 424px) {
    width: 100%;
    justify-content: center;
  }
  @media (max-width: 374px) {
    gap: 2px;
  }
  &__button {
    width: 50px;
    height: 50px;
    border-radius: 4px;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all $transition;
    &:hover {
      background-color: $base-200;
    }
    &:active {
      background-color: $green;
      svg {
        stroke: $white;
      }
    }
    svg {
      width: 16px;
      height: 16px;
      stroke: $base-900;
      stroke-width: 1.6;
      transition: all $transition;
    }
    &_disabled {
      pointer-events: none;
      svg {
        stroke: $base-700;
      }
    }
  }
  &__list {
    display: flex;
    gap: 6px;
    @media (max-width: 374px) {
      gap: 2px;
    }
  }
  &__item {
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    @include body-medium;
    color: $base-700;
    transition: all $transition;
    @media (max-width: 767px) {
      &:nth-last-child(2), &:nth-last-child(3) {
        display: none;
      }
    }
    &:not(.pagination__item_active) {
      &:hover {
        background-color: $base-200;
      }
      &:active {
        background-color: $green;
        color: $white;
      }
    }
    &_active {
      background-color: $green;
      color: $white;
    }
  }
}