.nav {
  &__list {
    display: flex;
    align-items: center;
    gap: 24px;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__item {
    display: flex;
    position: relative;
    @media (max-width: 1279px) {
      display: block;
    }
    &_active {
      .nav__link {
        color: $gold;
        @media (max-width: 1279px) {
          color: $green;
          border-color: $green;
        }
        svg {
          stroke: $gold;
          transform: rotate(-180deg);
          @media (max-width: 1279px) {
            stroke: $green;
          }
        }
      }
    }
  }
  &__link {
    display: flex;
    align-items: center;
    gap: 2px;
    @include body;
    color: $white;
    position: relative;
    transition: all $transition;
    z-index: 1;
    @media (min-width: 1280px) {
      &:hover {
        color: $gold;
        svg {
          stroke: $gold;
          transform: rotate(-180deg);
        }
      }
      &:active {
        color: $gold-active;
        svg {
          stroke: $gold-active;
        }
      }
    }
    @media (max-width: 1279px) {
      width: 100%;
      justify-content: space-between;
      padding: 16px 0 15px;
      color: $base-900;
      border-bottom: 1px solid $base-200;
    }
    svg {
      flex-shrink: 0;
      width: 16px;
      height: 16px;
      stroke: $white;
      transition: all $transition;
      @media (max-width: 1279px) {
        stroke: $base-900;
        transform: rotate(-90deg);
      }
    }
  }
  &__dropdown {
    display: none;
    position: absolute;
    top: calc(100% + 12px);
    left: -24px;
    // width: max-content;
    // min-width: 350px;
    // max-width: 1018px;
    padding: 24px;
    background-color: $white;
    border-radius: 2px;
    @include shadow;
    // box-shadow: 0 12px 54px rgba(#00150C, .7);
    @media (max-width: 1279px) {
      position: static;
      padding: 0;
      background: none;
      border-radius: none;
      box-shadow: none;
    }
    &::before {
      content: '';
      position: absolute;
      bottom: 100%;
      left: 0;
      width: 100%;
      height: 15px;
      opacity: 0;
      @media (max-width: 1279px) {
        content: none;
      }
    }
    &-inner {
      display: flex;
      gap: 24px;
      @media (max-width: 1279px) {
        display: block;
      }
    }
    &-list {
      > li {
        .nav__dropdown-sublist {
          @media (min-width: 1280px) {
            display: none !important;
          }
        }
      }
    }
    &-list, &-sublist {
      li {
        &:not(:last-child) {
          margin-bottom: 12px;
          @media (max-width: 1279px) {
            margin-bottom: 0;
          }
        }
      }
      a {
        min-width: 338px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: $gap;
        padding: 12px 0 11px;
        border-bottom: 1px solid $base-200;
        @include body;
        transition: all $transition;
        @media (max-width: 1279px) {
          min-width: auto;
          padding: 16px 0 15px 16px;
        }
        &.active {
          border-color: $green;
          color: $green;
          svg {
            stroke: $green;
            @media (max-width: 1279px) {
              transform: rotate(-90deg);
            }
          }
        }
        @media (min-width: 1280px) {
          &:hover {
            border-color: $green;
            color: $green;
            svg {
              stroke: $green;
            }
          }
          &:active {
            border-color: $green-active;
            color: $green-active;
            svg {
              stroke: $green-active;
            }
          }
        }
        svg {
          width: 16px;
          height: 16px;
          flex-shrink: 0;
          stroke: $base-900;
          transition: all $transition;
        }
      }
    }
    &-block {
      @media (max-width: 1279px) {
        display: none;
      }
    }
    &-sublist {
      @media (max-width: 1279px) {
        display: none;
        a {
          padding-left: 32px;
        }
      }
    }
    &-item {
      display: none;
      &.active {
        display: block;
      }
    }
  }
}