.tab {
  // @media (max-width: 1279px) {
  //   display: flex;
  //   margin-left: -30px;
  //   margin-right: -30px;
  //   margin-bottom: 30px;
  //   overflow-x: auto;
  //   -ms-overflow-style: none;
  //   scrollbar-width: none;
  //   &::-webkit-scrollbar {
  //     width: 0;
  //     height: 0;
  //   }
  // }
  // @media (max-width: 767px) {
  //   margin-left: -16px;
  //   margin-right: -16px;
  //   margin-bottom: 16px;
  // }
  &_white {
    .tab {
      &__inner {
        background-color: $white;
      }
      &__item {
        background-color: $white;
        &:not(.tab__item_active) {
          &:hover {
            background-color: $base-200;
          }
          &:active {
            background-color: $green;
            color: $white;
            svg {
              fill: $white;
            }
          }
        }
        &_active {
          background-color: $green;
          color: $white;
          svg {
            fill: $white;
          }
        }
      }
    }
  }
  &__inner {
    display: flex;
    gap: 4px;
    padding: 4px;
    background-color: $base-100;
    border-radius: 2px;
    // @media (max-width: 1279px) {
    //   gap: 12px;
    //   flex-shrink: 0;
    // }
  }
  &__item {
    flex-shrink: 0;
    height: 42px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 0 16px;
    background-color: $base-100;
    border-radius: 2px;
    @include body;
    transition: all $transition;
    // @media (max-width: 1279px) {
    //   &:first-child {
    //     margin-left: 30px;
    //   }
    //   &:last-child {
    //     margin-right: 30px;
    //   }
    // }
    // @media (max-width: 767px) {
    //   height: 37px;
    //   &:first-child {
    //     margin-left: 16px;
    //   }
    //   &:last-child {
    //     margin-right: 16px;
    //   }
    // }
    svg {
      width: 16px;
      height: 16px;
      flex-shrink: 0;
      fill: $base-900;
      transition: all $transition;
    }
    &:not(.tab__item_active) {
      &:hover {
        background-color: $base-200;
      }
      &:active {
        background-color: $green;
        color: $white;
        svg {
          fill: $white;
        }
      }
    }
    &_active {
      background-color: $green;
      color: $white;
      svg {
        fill: $white;
      }
    }
  }
  &-content {
    display: none;
    &_active {
      display: block;
    }
  }
}