.news-card {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: $white;
  border-radius: 2px;
  position: relative;
  overflow: hidden;
  transition: all $transition;
  &::after {
    content: '';
    position: absolute;
    inset: 0;
    border-radius: inherit;
    border: 1px solid $green;
    pointer-events: none;
    opacity: 0;
    transition: all $transition;
  }
  &:hover {
    @include shadow;
    &:after {
      opacity: 1;
    }
    .news-card__title {
      color: $green;
    }
  }
  &:active {
    &::after {
      border-color: $green-active;
    }
    .news-card__title {
      color: $green-active;
    }
  }
  &__img {
    width: 100%;
    height: 300px;
    @media (max-width: 1279px) {
      height: 240px;
    }
    img {
      @include cover;
    }
  }
  &__text {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding: 16px;
  }
  &__date, &__description {
    @include body;
    color: $base-700;
    margin-bottom: 16px;
  }
  &__title {
    @include h4;
    margin-bottom: 12px;
    transition: all $transition;
    @media (max-width: 1279px) {
      font-size: 15px;
    }
  }
  &__description {
    @media (max-width: 1279px) {
      font-size: 13px;
    }
  }
  .tag {
    margin-top: auto;
  }
}