.document {
  display: flex;
  justify-content: space-between;
  gap: 26px;
  padding: 24px;
  background-color: $base-100;
  border-radius: 2px;
  position: relative;
  z-index: 1;
  transition: all $transition;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 1px solid $green;
    border-radius: inherit;
    opacity: 0;
    transition: all $transition;
    z-index: -1;
    pointer-events: none;
  }
  &:hover {
    @include shadow;
    &::before {
      opacity: 1;
    }
  }
  &__title {
    @include body-medium;
    margin-bottom: 10px;
    @media (max-width: 767px) {
      font-size: 13px;
    }
  }
  &__date {
    @include label;
    color: $base-700;
  }
  &__button {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-shrink: 0;
    &-img {
      width: 18px;
      height: 18px;
      svg {
        width: 100%;
        height: 100%;
        stroke: $green;
        stroke-width: 1.5;
        stroke-linecap: round;
        stroke-linejoin: round;
      }
    }
    &-title {
      @include body-medium;
      color: $green;
    }
  }
}