.layout {
  @include section;
  &__title {
    padding-top: 4px;
    @include h2;
    margin-bottom: 20px;
  }
  &__content {
    margin-bottom: 24px;
  }
  &__item {
    display: grid;
    grid-template-columns: 102px auto 268px;
    gap: 0 $gap;
    align-items: flex-end;
    padding: 24px 0 23px;
    border-bottom: 1px solid $base-300;
    @media (max-width: 1279px) {
      padding: 12px 0 11px;
      gap: 0 12px;
      align-items: center;
    }
    @media (max-width: 767px) {
      grid-template-columns: 70px auto;
      gap: 12px;
    }
    &-img {
      grid-row: span 2;
      // width: 102px;
      height: 102px;
      flex-shrink: 0;
      border-radius: 4px;
      overflow: hidden;
      @media (max-width: 767px) {
        height: 70px;
        grid-row: span 1;
      }
      img {
        @include cover;
      }
    }
    &-title {
      margin-left: 8px;
      margin-bottom: 4px;
      grid-column: span 2;
      @include h4;
      @media (max-width: 1919px) {
        font-size: 20px;
      }
      @media (max-width: 1279px) {
        margin: 0;
      }
      @media (max-width: 767px) {
        grid-column: span 1;
        font-size: 17px;
      }
    }
    &-info {
      margin-left: 8px;
      margin-bottom: 3px;
      display: grid;
      grid-template-columns: repeat(5, 1fr);
      gap: $gap;
      @media (max-width: 1279px) {
        grid-column: span 2;
        gap: 12px;
        align-self: flex-end;
        margin-left: 0;
        margin-bottom: -8px;
      }
      @media (max-width: 767px) {
        grid-template-columns: repeat(2, 1fr);
        margin-bottom: 0;
      }
      &-title {
        @include label;
        color: $base-700;
        margin-bottom: 8px;
      }
      &-value {
        @include body-medium;
      }
    }
    &-button {
      margin-bottom: 3px;
      @media (max-width: 1279px) {
        grid-column: 2 / span 2;
        width: 268px;
        margin-bottom: 0;
        margin-top: 27px;
      }
      @media (max-width: 767px) {
        grid-column: span 2;
        margin-top: 0;
      }
    }
  }
}