.info {
  @include section;
  background-color: $white;
  &__body {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    @media (max-width: 1600px) {
      &:not(:has(.info__title)) {
        display: block;
      }
    }
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__title {
    @include h5;
    @media (max-width: 1279px) {
      padding-top: 2px;
      margin-bottom: 22px;
    }
    @media (max-width: 767px) {
      font-size: 21px;
      margin-bottom: 24px;
    }
  }
  &__content {
    grid-column: 2 / span 3;
  }
  &__description {
    @include h3;
    &:not(:last-child) {
      margin-bottom: 40px;
      @media (max-width: 1279px) {
        margin-bottom: 20px;
      }
    }
  }
  &__button {
    width: 293px;
  }
  &__subtitle {
    @include h4;
    color: $base-700;
  }
  &__group {
    @media (max-width: 1279px) {
      display: inline-block;
      margin-top: 4px;
    }
  }
  &__list {
    padding-left: 30px;
    list-style-type: disc;
    @media (max-width: 1600px) {
      padding-left: 26px;
    }
    @media (max-width: 767px) {
      padding-left: 22px;
    }
    li {
      @include h4;
      color: $base-700;
      @media (max-width: 767px) {
        line-height: 1.27;
      }
    }
  }
}