.footer {
  background-color: $base-900;
  color: $white;
  &__content {
    padding: 170px 0 30px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      padding: 100px 0 30px;
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  .logo {
    width: 242px;
    height: 62px;
    @media (max-width: 1279px) {
      grid-column: span 4;
    }
    @media (max-width: 767px) {
      grid-column: span 2;
    }
  }
  &__nav {
    padding-top: 20px;
    @media (max-width: 1279px) {
      padding-top: 4px;
      &:last-child {
        grid-column: span 2;
        @media (max-width: 767px) {
          padding-top: 21px;
        }
      }
    }
    &-title {
      @include h5;
      margin-bottom: 20px;
      @media (max-width: 1279px) {
        font-size: 18px;
      }
    }
  }
  &__list {
    li {
      &:not(:last-child) {
        margin-bottom: 16px;
        @media (max-width: 767px) {
          margin-bottom: 12px;
        }
      }
    }
    a {
      @include body;
      color: $base-100;
      transition: all $transition;
      &:hover {
        color: $gold;
      }
      &:active {
        color: $gold-active;
      }
    }
  }
  &__info {
    padding: 29px 0;
    border-top: 1px solid #434343;
    border-bottom: 1px solid #434343;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(2, 1fr);
      gap: 32px 16px;
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
    }
    &-item {
      @include body;
      @media (max-width: 767px) {
        padding-right: 4px;
      }
    }
    &-title {
      @include h4;
      margin-bottom: 24px;
      @media (max-width: 1279px) {
        font-size: 20px;
        margin-bottom: 8px;
      }
      @media (max-width: 767px) {
        font-size: 20px;
      }
    }
    &-address {
      margin-bottom: 16px;
      @media (max-width: 1279px) {
        margin-bottom: 8px;
      }
    }
    &-link {
      color: $base-100;
      transition: all $transition;
      &:hover {
        color: $gold;
      }
      &:active {
        color: $gold-active;
      }
    }
  }
  &__bottom {
    padding: 30px 0 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: $gap;
    @media (max-width: 1279px) {
      padding-bottom: 40px;
    }
    @media (max-width: 767px) {
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      gap: 12px;
    }
    &-link {
      display: flex;
      align-items: center;
      @include body;
      color: rgba($white, .5);
      transition: all $transition;
      &:hover {
        color: $gold;
      }
      &:active {
        color: $gold-active;
      }
    }
  }
  &__copy {
    @include body;
    color: rgba($white, .5);
  }
}