.location {
  padding: 40px 0;
  &__heading {
    margin-bottom: 20px;
  }
  &__title {
    padding-top: 4px;
    @include h2;
  }
  &__map {
    width: 100%;
    height: 781px;
    @media (max-width: 767px) {
      height: 570px;
    }
    [class*=-balloon] {
      box-shadow: none;
    }
    [class*=-balloon__content] {
      padding: 0 !important;
      margin: 0 !important;
    }
    [class*=-balloon__layout] {
      @include shadow;
    }
    [class*=-balloon__close] {
      display: none;
    }
    [class*=-balloon__tail] {
      display: none;
    }
    .location-balloon {
      width: 256px;
      padding: 8px;
      background-color: $white;
      &__img {
        width: 100%;
        height: 170px;
        margin-bottom: 8px;
        img {
          @include cover;
        }
      }
      &__title {
        @include body-medium;
        @include inter;
        color: $base-900;
      }
    }
  }
}