.not-found {
  @media (max-width: 767px) {
    overflow: hidden;
  }
  &__content {
    min-height: calc(100vh - 267px);
    padding: 125px 0 75px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @media (max-width: 1279px) {
      // min-height: auto;
      // padding: 245px 0 120px;
      min-height: calc(100vh - 120px);
      padding: 150px 0 60px;
    }
  }
  &__number {
    display: inline-flex;
    align-items: center;
    height: 306px;
    font-size: 200px;
    @include angst;
    position: relative;
    margin-right: 45px;
    margin-bottom: 18px;
    @media (max-width: 1279px) {
      height: 200px;
      font-size: 120px;
      margin-right: 30px;
      margin-bottom: 80px;
    }
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: calc(100% - 36px);
      transform: translateY(-57%) skewX(-23deg);
      width: 80px;
      height: 100%;
      border-left: 2px solid $base-900;
      background-color: $base-100;
      @media (max-width: 1279px) {
        left: calc(100% - 7px);
        transform: translateY(-55%) skewX(-23.5deg);
      }
      @media (max-width: 767px) {
        width: 50px;
      }
    }
  }
  &__description {
    @include body-medium;
    text-align: center;
    margin-bottom: 24px;
    @media (max-width: 767px) {
      font-size: 13px;
    }
  }
  &__button {
    @media (max-width: 374px) {
      padding: 0 8px;
    }
  }
}