.filter {
  margin-top: -256px;
  position: relative;
  z-index: 1;
  @media (max-width: 1023px) {
    margin-top: -316px;
  }
  @media (max-width: 767px) {
    margin-top: -136px;
  }
  &__body {
    padding: 24px;
    background-color: $white;
    border-radius: 4px;
    @media (max-width: 767px) {
      padding: 16px 8px;
    }
  }
  &__title {
    @include h4;
    margin-bottom: 24px;
    @media (max-width: 767px) {
      @include h3;
      margin-bottom: 16px;
    }
  }
  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 500px 1fr;
    gap: 24px $gap;
    margin-bottom: 24px;
    @media (max-width: 1600px) {
      grid-template-columns: 1fr 1fr 155px 420px 1fr;
    }
    @media (max-width: 1439px) {
      grid-template-columns: repeat(6, 1fr);
    }
    @media (max-width: 1023px) {
      grid-template-columns: repeat(2, 1fr);
      gap: $gap;
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
      margin-bottom: 16px;
      overflow: hidden;
    }
  }
  &__item {
    @media (max-width: 1439px) {
      grid-column: span 2;
    }
    @media (max-width: 1023px) {
      grid-column: span 1;
    }
    &_price, &_area {
      @media (max-width: 1439px) {
        grid-column: span 3;
      }
    }
    &_price {
      @media (max-width: 1023px) {
        order: +1;
        grid-column: span 2;
        @media (max-width: 767px) {
          grid-column: span 1;
        }
      }
    }
    &-title {
      @include body;
      color: $base-700;
      margin-bottom: 8px;
    }
    &-group {
      display: flex;
      transition: all $transition;
      border: 1px solid $base-100;
      background-color: $base-100;
      border-radius: 2px;
      &:hover {
        border-color: $base-300;
      }
      .labels {
        height: 48px;
        align-items: center;
        padding-right: 16px;
        @media (max-width: 767px) {
          display: none;
        }
        .label {
          height: 33px;
          &__text {
            white-space: nowrap;
          }
        }
      }
      .select2 {
        &-block {
          display: none;
          @media (max-width: 767px) {
            display: block;
            align-self: center;
            width: 85px;
            flex-shrink: 0;
            margin-right: 4px;
          }
        }
        &-selection {
          height: 33px !important;
          &__rendered {
            padding: 0 8px !important;
            line-height: 31px !important;
          }
          &__arrow {
            right: 8px !important;
          }
        }
      }
    }
  }
  &__search {
    grid-column: span 4;
    position: relative;
    @media (max-width: 1023px) {
      grid-column: span 2;
      order: +1;
    }
    @media (max-width: 767px) {
      grid-column: span 1;
    }
    input {
      padding-right: 170px;
    }
  }
  &__location {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 16px;
    @include body;
    transition: all $transition;
    z-index: 1;
    svg {
      width: 20px;
      height: 20px;
      fill: $base-900;
      transition: all $transition;
    }
    &:hover {
      color: $green;
      svg {
        fill: $green;
      }
    }
    &:hover {
      color: $green-active;
      svg {
        fill: $green-active;
      }
    }
  }
  &__tags {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    @media (max-width: 767px) {
      margin: 0 -8px;
      flex-wrap: nowrap;
      overflow-x: auto;
      -ms-overflow-style: none;
      scrollbar-width: none;
      &::-webkit-scrollbar {
        display: none;
        -webkit-appearance: none;
        width: 0;
        height: 0;
      }
      .label {
        flex-shrink: 0;
        &:first-child {
          margin-left: 8px;
        }
        &:last-child {
          margin-right: 8px;
        }
      }
    }
  }
  &__button {
    @media (max-width: 1600px) {
      padding: 0 16px;
    }
    @media (max-width: 1439px) {
      grid-column: span 2;
    }
    @media (max-width: 1023px) {
      order: +1;
    }
    @media (max-width: 767px) {
      grid-column: span 1;
    }
  }
}