.hero-template {
  position: relative;
  z-index: 1;
  color: $white;
  @media (max-width: 767px) {
    overflow: hidden;
  }
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -2;
    img {
      @include cover;
    }
  }
  &__content {
    min-height: 400px;
    padding: 89px 0 50px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: $gap;
    @media (max-width: 1279px) {
      min-height: 320px;
    }
  }
  &__title {
    @include h1;
    &:not(:last-child) {
      margin-bottom: 10px;
      @media (max-width: 767px) {
        margin-bottom: 14px;
      }
    }
  }
  &__description {
    max-width: 800px;
    @include body-medium;
    margin-bottom: 32px;
    @media (max-width: 767px) {
      font-size: 13px;
    }
  }
  &__button {
    width: 293px;
    @media (max-width: 1279px) {
      width: 268px;
    }
  }
  &_catalog {
    ~ .filter {
      margin-top: -166px;
    }
    .hero-template {
      &__content {
        min-height: 527px;
        padding-bottom: 192px;
        @media (max-width: 767px) {
          padding-bottom: 188px;
        }
      }
    }
  }
  &_services {
    .hero-template {
      &__content {
        min-height: 527px;
        @media (max-width: 1279px) {
          min-height: 443px;
        }
      }
    }
  }
  &_news_detail {
    .hero-template {
      &__title {
        max-width: 1250px;
        @include h2;
      }
    }
  }
}