.range {
  width: 100%;
  display: flex;
  position: relative;
  .label {
    flex: 1;
    align-items: center;
    gap: 4px;
    padding-left: 15px;
    &:first-child {
      &::after {
        content: '—';
        position: absolute;
        top: 50%;
        left: 100%;
        transform: translate(-100%, -50%);
        color: $base-700;
        pointer-events: none;
        @media (max-width: 1600px) {
          transform: translate(-50%, -50%);
        }
      }
    }
    span {
      @include body;
      color: $base-700;
    }
  }
  input {
    height: 48px;
    border: none;
    background: none;
    padding-left: 0;
  }
}

.range-slider {
  position: absolute;
  bottom: 0;
  left: 22px;
  right: 22px;
  height: 2px;
  // background: $base-300;
  background: transparent;
  border: none;
  border-radius: 0;
  box-shadow: none;
  z-index: 1;
  &.noUi-state-drag {
    .noUi {
      &-connect {
        background-color: $green;
      }
      &-handle {
        background: $green;
      }
    }
  }
  .noUi {
    &-connect {
      background-color: $base-900;
    }
    &-handle {
      width: 12px;
      height: 12px;
      top: -5px;
      right: -6px;
      background: $base-900;
      border: none;
      border-radius: 50%;
      box-shadow: none;
      cursor: pointer;
      &::before, &::after {
        content: none;
      }
    }
  }
}