.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 500;
  // background-color: $white;
  transition: all $transition, padding-right 0s;
  @media (max-width: 1279px) {
    .container {
      padding: 0;
    }
    &_active {
      .header {
        &__body {
          background-color: $white;
          .logo {
            svg {
              fill: $green;
            }
          }
        }
        &__button {
          background-color: $green;
          color: $white;
          &:hover {
            background-color: $green-active;
          }
          &:active {
            background-color: $base-900;
          }
        }
      }
    }
  }
  &_scroll, &_white {
    background-color: $white;
    @media (max-width: 1279px) {
      background: none;
      .burger {
        background-color: $base-100;
      }
    }
    .header {
      &__body {
        @media (max-width: 1279px) {
          background-color: $white;
        }
        .logo {
          svg {
            fill: $green;
          }
        }
      }
      &__button {
        background-color: $green;
        color: $white;
        &:hover {
          background-color: $green-active;
        }
        &:active {
          background-color: $base-900;
        }
      }
    }
    .nav {
      &__item {
        &_active {
          .nav__link {
            color: $green;
            svg {
              stroke: $green;
            }
          }
        }
      }
      &__link {
        color: $base-900;
        @media (min-width: 1280px) {
          &:hover {
            color: $green;
            svg {
              stroke: $green;
            }
          }
          &:active {
            color: $green-active;
            svg {
              stroke: $green-active;
            }
          }
        }
        svg {
          stroke: $base-900;
        }
      }
    }
  }
  &__body {
    // padding: 24px 0;
    padding: 14px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: $gap;
    transition: all $transition;
    @media (max-width: 1279px) {
      padding: 14px 16px;
    }
  }
  &__content {
    flex-grow: 1;
    display: flex;
    align-items: center;
    gap: $gap;
    @media (max-width: 1279px) {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      height: 100vh;
      height: 100dvh;
      margin: 0;
      z-index: -1;
      transform: translateY(-150%);
      transition: all .4s ease;
      padding: 90px 16px 16px;
      background: $base-100;
      flex-direction: column;
      align-items: flex-start;
      overflow-x: hidden;
      overflow-y: auto;
      &_active {
        transform: translateY(0%);
      }
    }
  }
  .nav {
    margin: 0 auto;
    @media (max-width: 1279px) {
      width: 100%;
      margin: 0;
    }
  }
  &__button.button {
    flex-shrink: 0;
    height: 45px;
    @media (max-width: 1279px) {
      margin-top: auto;
    }
    @media (max-width: 767px) {
      width: 100%;
    }
  }
}