.offer {
  @include section;
  overflow: hidden;
  &__heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: $gap;
    margin-bottom: 24px;
  }
  &__title {
    @include h2;
  }
  &__slider {
    overflow: visible;
    &-item {
      display: flex;
      flex-direction: column;
      gap: 16px;
      height: auto;
      min-height: 326px;
      padding: 24px;
      background-color: $white;
      transition: all $transition;
      z-index: 1;
      @media (max-width: 1279px) {
        min-height: 279px;
        padding: 16px;
      }
      &:hover {
        @include shadow;
        &:before {
          opacity: 1;
        }
        .button {
          background-color: $green-active;
        }
      }
      &:active {
        &:before {
          border-color: $green-active;
        }
        .button {
          background-color: $base-900;
        }
      }
      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: 1px solid $green;
        opacity: 0;
        transition: all $transition;
        z-index: -1;
        pointer-events: none;
      }
      &-title {
        @include h4;
        @media (max-width: 1279px) {
          font-size: 15px;
        }
      }
      &-description {
        @include body;
        color: $base-700;
        @media (max-width: 1279px) {
          font-size: 13px;
        }
        p {
          &:not(:last-child) {
            margin-bottom: 16px;
          }
        }
      }
      &-button {
        width: 100%;
        margin-top: auto;
        pointer-events: none;
      }
    }
  }
}