.catalog {
  @include section;
  .container {
    &_wide {
      max-width: none;
      padding: 0;
    }
  }
  &__heading {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    align-items: center;
    margin-bottom: 24px;
    @media (max-width: 1600px) {
      grid-template-columns: repeat(3, 1fr);
    }
    @media (max-width: 1279px) {
      display: flex;
      justify-content: space-between;
      .select2-block {
        width: 204px;
      }
    }
    @media (max-width: 767px) {
      display: block;
      .select2-block {
        width: 100%;
      }
    }
  }
  &__title {
    grid-column: span 3;
    @include h2;
    @media (max-width: 1600px) {
      grid-column: span 2;
    }
    @media (max-width: 1279px) {
      transform: translateY(4px);
    }
    @media (max-width: 767px) {
      padding-top: 2px;
      transform: none;
      margin-bottom: 14px;
    }
  }
  &__filter {
    display: flex;
    justify-content: space-between;
    gap: $gap;
    padding: 3px 0;
    @media (max-width: 1279px) {
      gap: 8px;
    }
    .tab {
      flex-shrink: 0;
      &__item {
        min-width: 50px;
        @media (max-width: 767px) {
          span {
            display: none;
          }
        }
      }
    }
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    margin-bottom: 24px;
    @media (max-width: 1600px) {
      grid-template-columns: repeat(3, 1fr);
    }
    @media (max-width: 1279px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__body {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    height: 944px;
    position: relative;
    @media (max-width: 1600px) {
      grid-template-columns: 638px auto;
    }
    @media (max-width: 1279px) {
      display: block;
    }
    @media (max-width: 767px) {
      height: 570px;
    }
    .card {
      display: none;
      &::before {
        content: none;
      }
      @media (max-width: 1279px) {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 392px;
        z-index: 1;
        @include shadow;
        &_active {
          display: block;
        }
      }
      @media (max-width: 424px) {
        width: auto;
        left: 16px;
        right: 16px;
        transform: translateY(-50%);
      }
    }
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: 8px;
    height: 100%;
    overflow-y: auto;
    @media (max-width: 1279px) {
      display: none;
    }
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: $base-200;
    }
    &::-webkit-scrollbar-thumb {
      width: 100%;
      background: $green;
    }
  }
  &__map {
    grid-column: span 2;
    height: 100%;
    @media (max-width: 1600px) {
      grid-column: span 1;
    }
  }
  &__button {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    width: 295px;
  }
}