.project {
  padding: 90px 0 0;
  background-color: $white;
  @media (max-width: 1279px) {
    overflow: hidden;
  }
  &__content {
    margin-top: 8px;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__gallery {
    height: 758px;
    min-width: 0;
    grid-column: span 7;
    position: relative;
    margin-left: -40px;
    @media (max-width: 1600px) {
      grid-column: span 6;
      margin-right: -40px;
    }
    @media (max-width: 1365px) {
      margin-right: 0;
    }
    @media (max-width: 1279px) {
      height: 600px;
      margin-left: -16px;
      margin-right: -16px;
    }
    @media (max-width: 767px) {
      height: 500px;
    }
  }
  &__slider {
    width: 100%;
    height: 100%;
    &-item {
      height: 100%;
      img {
        @include cover;
      }
    }
  }
  &__thumbs {
    position: absolute;
    max-width: calc(100% - 32px);
    left: 50%;
    transform: translateX(-50%);
    bottom: 24px;
    padding: 8px;
    background-color: $white;
    border-radius: 2px;
    @media (max-width: 767px) {
      max-width: calc(100% - 24px);
      bottom: 12px;
      padding: 4px;
    }
    &-item {
      width: 70px;
      height: 70px;
      cursor: pointer;
      @media (max-width: 767px) {
        width: 40px;
        height: 40px;
      }
      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: 2px solid $gold;
        opacity: 0;
        transition: all $transition;
      }
      &.swiper-slide-thumb-active {
        &::after {
          opacity: 1;
        }
      }
      &:not(:last-child) {
        margin-right: 8px;
        @media (max-width: 767px) {
          margin-right: 4px;
        }
      }
      img {
        @include cover;
      }
    }
  }
  &__text {
    height: 758px;
    grid-column: span 5;
    padding: 24px 0 24px 24px;
    display: flex;
    flex-direction: column;
    gap: 24px;
    @media (max-width: 1600px) {
      grid-column: span 6;
      padding-left: 63px;
    }
    @media (max-width: 1365px) {
      padding-left: 24px;
    }
    @media (max-width: 1279px) {
      height: auto;
      padding: 24px 0;
    }
    .tag {
      gap: 8px;
      @media (max-width: 767px) {
        margin: 0 -16px;
        width: calc(100% + 32px);
        overflow-x: auto;
        scrollbar-width: none;
        flex-wrap: nowrap;
      }
      &__item {
        @media (max-width: 767px) {
          white-space: nowrap;
          &:first-child {
            margin-left: 16px;
          }
          &:last-child {
            margin-right: 16px;
          }
        }
      }
    }
  }
  &__title {
    @include h5;
  }
  &__subtitle {
    @include h4;
    margin-bottom: -8px;
  }
  &__characteristics {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px 32px;
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
    }
    &-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 8px;
      position: relative;
      &::before {
        content: '';
        flex-grow: 1;
        height: 1px;
        align-self: flex-end;
        background-color: $base-200;
        margin-bottom: 5px;
      }
      &-title {
        @include body;
        order: -1;
        @media (max-width: 767px) {
          font-size: 13px;
        }
      }
      &-value {
        @include body-medium;
        @media (max-width: 767px) {
          font-size: 13px;
        }
      }
    }
  }
  &__description {
    flex-grow: 1;
    padding-right: 16px;
    @include body;
    color: $base-700;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: $base-200;
    }
    &::-webkit-scrollbar-thumb {
      width: 100%;
      background: $green;
    }
  }
  &__button {
    &s {
      margin-top: auto;
      display: grid;
      grid-template-columns: repeat(2, 268px);
      gap: $gap;
      @media (max-width: 767px) {
        grid-template-columns: repeat(1, 1fr);
      }
    }
  }
}