.about {
  position: relative;
  &_other {
    .about__img {
      &::before, &::after {
        content: none;
      }
    }
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    &::before, &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba($base-900, .08) 0%, rgba($base-900, 1) 100%);
    }
    &::before {
      opacity: .6;
    }
    img {
      @include cover;
    }
  }
  &__content {
    min-height: 968px;
    padding: 60px 0;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    @media (max-width: 1600px) {
      min-height: 820px;
    }
    @media (max-width: 1279px) {
      padding: 40px 0;
    }
    @media (max-width: 767px) {
      min-height: 700px;
    }
  }
  &__text {
    text-align: center;
    color: $white;
  }
  &__title {
    @include h2;
    margin-bottom: 20px;
    @media (max-width: 1600px) {
      padding: 0 16px;
    }
    @media (max-width: 1279px) {
      padding: 0;
    }
    span {
      color: $gold;
    }
  }
  &__description {
    max-width: 912px;
    @include h4;
    color: $base-100;
    margin: 0 auto 46px;
    @media (max-width: 1919px) {
      font-size: 20px;
    }
    @media (max-width: 1279px) {
      @include h4;
    }
  }
  &__bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 46px 0 40px;
    background-color: $green;
    color: $white;
    @media (max-width: 1279px) {
      padding: 20px 0 16px;
    }
    &-inner {
      max-width: 1420px;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      gap: $gap;
      @media (max-width: 1279px) {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 37px $gap;
      }
      @media (max-width: 767px) {
        gap: 28px $gap;
      }
    }
    &-item {
      @media (max-width: 1279px) {
        text-align: center;
      }
      &-title {
        @include h1;
        margin-bottom: 2px;
        @media (max-width: 1279px) {
          margin-bottom: 3px;
        }
      }
      &-subtitle {
        @include h4;
      }
    }
  }
}