.card {
  min-width: 0;
  display: flex;
  flex-direction: column;
  background-color: $white;
  border-radius: 2px;
  position: relative;
  transition: all $transition, border-radius 0s;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
    border: 1px solid $base-300;
    border-bottom: none;
    opacity: 0;
    z-index: 2;
    transition: all $transition;
  }
  @media (min-width: 1280px) {
    &:hover {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      @include shadow;
      &::before {
        opacity: 1;
      }
      .slider-pagination {
        opacity: 1;
      }
      .card__info {
        opacity: 1;
        // visibility: visible;
        pointer-events: all;
      }
    }
  }
  &__heading {
    position: relative;
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
    overflow: hidden;
  }
  &__top {
    position: absolute;
    top: 10px;
    left: 10px;
    right: 10px;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    gap: $gap;
    .tag {
      gap: 8px;
      &__item {
        padding: 7px 15px;
        background-color: $white;
        border: 1px solid $base-200;
      }
    }
  }
  &__download {
    height: 32px;
    gap: 4px;
    padding: 0 8px;
    @include label;
    svg {
      width: 18px;
      height: 18px;
      flex-shrink: 0;
      stroke: $white;
      stroke-width: 1.5;
      stroke-linecap: round;
      stroke-linejoin: round;
    }
    @media (max-width: 767px) {
      width: auto;
    }
  }
  &__slider {
    &-item {
      width: 100%;
      height: 404px;
      @media (max-width: 1279px) {
        height: 340px;
      }
      @media (max-width: 767px) {
        height: 300px;
      }
      img {
        @include cover;
      }
    }
    .slider-pagination {
      opacity: 0;
      @media (max-width: 1279px) {
        opacity: 1;
      }
    }
  }
  &__text {
    flex-grow: 1;
    display: block;
    padding: 16px;
    @media (max-width: 1279px) {
      padding: 8px;
    }
  }
  &__title {
    @include h4;
    transition: all $transition;
    margin-bottom: 12px;
  }
  &__id {
    @include label;
    color: $base-700;
    margin-bottom: 10px;
  }
  &__address {
    @include body;
    margin-bottom: 10px;
    @media (max-width: 767px) {
      font-size: 13px;
    }
  }
  &__location {
    display: flex;
    align-items: center;
    gap: 8px;
    position: relative;
    z-index: 4;
    &-item {
      display: flex;
      align-items: center;
      gap: 4px;
      @include label;
      &:not(:first-child) {
        color: $base-700;
      }
      svg {
        width: 16px;
        height: 16px;
        flex-shrink: 0;
      }
    }
  }
  &__info {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    z-index: 3;
    padding: 8px 16px 16px;
    background-color: $white;
    border: 1px solid $base-300;
    border-top: none;
    border-bottom-left-radius: 2px;
    border-bottom-right-radius: 2px;
    opacity: 0;
    // visibility: hidden;
    pointer-events: none;
    transition: all $transition;
    @include shadow;
    @media (max-width: 1279px) {
      display: none;
    }
    &::before {
      content: '';
      position: absolute;
      bottom: 100%;
      left: 0;
      right: 0;
      background-color: $white;
      height: 32px;
      pointer-events: none;
    }
    &-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 8px;
      position: relative;
      &:not(:last-child) {
        margin-bottom: 10px;
      }
      &::before {
        content: '';
        flex-grow: 1;
        height: 1px;
        align-self: flex-end;
        background-color: $base-200;
        margin-bottom: 5px;
      }
      &-title {
        @include body;
        order: -1;
      }
      &-value {
        @include body-medium;
      }
    }
  }
  &__bottom {
    padding: 8px;
    display: flex;
    flex-direction: column;
    gap: $gap;
  }
}