.completed-projects {
  @include section;
  overflow: hidden;
  &__heading {
    padding-top: 4px;
    margin-bottom: 24px;
  }
  &__title {
    @include h5;
    margin-bottom: 19px;
  }
  &__block {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: $gap;
    @media (max-width: 767px) {
      display: block;
    }
  }
  &__filter {
    width: max-content;
    border-top: 1px solid $base-300;
    @media (max-width: 767px) {
      width: auto;
      margin: 0 -16px;
    }
    &-inner {
      display: flex;
      align-items: center;
      gap: 41px;
      margin-top: -1px;
      @media (max-width: 767px) {
        overflow-x: auto;
        padding: 0 16px;
        -ms-overflow-style: none;
        scrollbar-width: none;
        &::-webkit-scrollbar {
          width: 0;
          height: 0;
        }
      }
    }
    &-item {
      min-height: 50px;
      display: flex;
      align-items: center;
      padding: 12px 0;
      @include h4;
      color: $base-700;
      position: relative;
      transition: all $transition;
      // @media (max-width: 1600px) {
      //   font-size: 20px;
      // }
      @media (max-width: 767px) {
        font-size: 17px;
      }
      &:not(.completed-projects__filter-item_active) {
        &:hover {
          color: $base-900;
        }
      }
      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: $green;
        opacity: 0;
        transition: all $transition;
      }
      &_active {
        color: $green;
        &::before {
          opacity: 1;
        }
      }
    }
  }
  &__slider {
    overflow: visible;
    &-item {
      height: auto;
      background-color: $white;
      &-img {
        width: 100%;
        height: 519px;
        @media (max-width: 1279px) {
          height: 400px;
        }
        img {
          @include cover;
        }
      }
      &-text {
        padding: 28px 16px 22px;
      }
      &-title {
        @include h5;
        @media (max-width: 1279px) {
          font-size: 22px;
        }
      }
    }
  }
}