.quote {
  @include section;
  background-color: $white;
  &__body {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__img {
    grid-column: 2;
    width: 100px;
    height: 100px;
    @media (max-width: 1279px) {
      width: 64px;
      height: 64px;
      margin-bottom: 25px;
    }
    @media (max-width: 767px) {
      margin-bottom: 28px;
    }
    svg {
      width: 100%;
      height: 100%;
    }
  }
  &__content {
    padding-top: 3px;
    grid-column: 4 / span 8;
    @media (max-width: 1600px) {
      grid-column: 5 / span 7;
    }
    @media (max-width: 1279px) {
      padding-top: 0;
    }
  }
  &__description {
    @include h5;
    p {
      &:not(:last-child) {
        margin-bottom: 36px;
        @media (max-width: 1600px) {
          margin-bottom: 22px;
        }
        @media (max-width: 1279px) {
          margin-bottom: 24px;
        }
      }
    }
  }
}