.feedback {
  margin-bottom: -110px;
  position: relative;
  @media (max-width: 1279px) {
    margin-bottom: -60px;
  }
  &__body {
    max-width: 1530px;
    margin: 0 auto;
    display: flex;
    background-color: $white;
    border-radius: 2px;
    overflow: hidden;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__img {
    width: 510px;
    height: auto;
    flex-shrink: 0;
    @media (max-width: 1439px) {
      width: 450px;
    }
    @media (max-width: 1279px) {
      width: 100%;
      // aspect-ratio: 3/1;
      height: 250px;
    }
    img {
      @include cover;
    }
  }
  &__content {
    flex-grow: 1;
    padding: 43px 24px 40px;
    @media (max-width: 1439px) {
      padding: 43px 16px 40px;
    }
    @media (max-width: 1279px) {
      padding: 18px 16px 16px;
    }
  }
  &__title {
    @include h5;
    margin-bottom: 22px;
  }
  &__subtitle {
    @include h4;
    margin-bottom: 24px;
  }
  &__filter {
    margin-bottom: 20px;
    &-title {
      @include body;
      color: $base-700;
      margin-bottom: 8px;
    }
  }
  .tab {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $gap;
    @media (max-width: 767px) {
      display: none;
    }
    &__inner {
      grid-column: span 2;
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      @media (max-width: 1279px) {
        grid-column: span 3;
      }
    }
  }
  .select2-block {
    display: none;
    @media (max-width: 767px) {
      display: block;
    }
  }
  .form {
    &__button {
      @media (max-width: 1439px) {
        padding: 0 8px;
      }
    }
  }
}