.hero {
  position: relative;
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    img {
      @include cover;
    }
  }
  &__content {
    min-height: 845px;
    padding: 120px 0 320px;
    display: flex;
    justify-content: center;
    align-items: center;
    @media (max-width: 1279px) {
      padding-bottom: 364px;
    }
    @media (max-width: 767px) {
      min-height: 665px;
      align-items: flex-end;
      padding-bottom: 160px;
    }
  }
  &__text {
    max-width: 1100px;
    text-align: center;
    color: $white;
  }
  &__title {
    @include h1;
    margin-bottom: 26px;
    @media (max-width: 1919px) {
      font-size: 60px;
    }
    @media (max-width: 767px) {
      margin-bottom: 13px;
    }
    span {
      display: block;
      color: $gold;
      @media (max-width: 767px) {
        display: inline;
      }
    }
  }
  &__description {
    @include h4;
    @media (max-width: 1919px) {
      font-size: 20px;
    }
    @media (max-width: 1279px) {
      @include h4;
    }
    @media (max-width: 767px) {
      @include body;
      font-size: 13px;
    }
  }
}