.contacts {
  padding-bottom: 60px;
  overflow: hidden;
  @media (max-width: 1279px) {
    padding-bottom: 40px;
  }
  &__heading {
    padding: 60px 0;
    background-color: $white;
    @media (max-width: 1279px) {
      padding: 40px 0;
    }
  }
  &__item {
    padding-top: 16px;
    @include h4;
    border-top: 1px solid transparent;
    transition: all $transition;
    @media (max-width: 1279px) {
      width: 280px;
      flex-shrink: 0;
      @media (max-width: 767px) {
        width: 230px;
      }
      &:first-child {
        margin-left: 16px;
      }
      &:last-child {
        margin-right: 16px;
      }
    }
    &_active {
      border-color: $green;
      .contacts__item-city {
        color: $green;
      }
    }
    &s {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: $gap;
      @media (max-width: 1279px) {
        display: flex;
        overflow-x: auto;
        margin: 0 -16px;
        -ms-overflow-style: none;
        scrollbar-width: none;
        &::-webkit-scrollbar {
          width: 0;
          height: 0;
        }
      }
    }
    &-city {
      @include h5;
      letter-spacing: normal;
      margin-bottom: 12px;
      transition: all $transition;
    }
    &-address {
      margin-bottom: 16px;
    }
  }
  &__map {
    width: 100%;
    height: 685px;
    @media (max-width: 767px) {
      height: 570px;
    }
    [class*=-balloon] {
      box-shadow: none;
    }
    [class*=-balloon__content] {
      padding: 0 !important;
      margin: 0 !important;
    }
    [class*=-balloon__layout] {
      @include shadow;
    }
    [class*=-balloon__close] {
      display: none;
    }
    [class*=-balloon__tail] {
      display: none;
    }
    .location-balloon {
      width: 256px;
      padding: 8px;
      background-color: $white;
      &__img {
        width: 100%;
        height: 170px;
        &:not(:last-child) {
          margin-bottom: 8px;
        }
        img {
          @include cover;
        }
      }
      &__title {
        @include body-medium;
        @include inter;
        color: $base-900;
      }
    }
  }
}