.documents {
  @include section;
  background-color: $white;
  overflow: hidden;
  &__filter {
    margin-bottom: 24px;
  }
  .tab {
    overflow-x: auto;
    margin: 0 -40px;
    @media (max-width: 1279px) {
      margin: 0 -16px;
      -ms-overflow-style: none;
      scrollbar-width: none;
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
    }
    &__inner {
      display: inline-flex;
      min-width: calc(100% - 80px);
      margin: 0 40px;
      @media (max-width: 1279px) {
        min-width: calc(100% - 32px);
        margin: 0 16px;
      }
    }
    &__item {
      flex: 1;
      height: 50px;
      padding: 0 12px;
      white-space: nowrap;
      @media (max-width: 1279px) {
        padding: 0 16px;
      }
    }
  }
  &__heading {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    align-items: center;
    margin-bottom: 24px;
    @media (max-width: 1600px) {
      grid-template-columns: repeat(3, 1fr);
    }
    @media (max-width: 1279px) {
      display: flex;
      justify-content: space-between;
      .select2-block {
        min-width: 248px;
      }
    }
    @media (max-width: 767px) {
      display: block;
      .select2-block {
        min-width: auto;
      }
    }
  }
  &__title {
    grid-column: span 2;
    @include h5;
    @media (max-width: 1600px) {
      grid-column: span 1;
    }
    @media (max-width: 767px) {
      font-size: 21px;
      padding-top: 2px;
      margin-bottom: 22px;
    }
  }
  &__group {
    grid-column: 4;
    display: flex;
    gap: $gap;
    padding: 3px 0;
    &:has(.search) {
      grid-column: 3 / span 2;
      @media (max-width: 1600px) {
        grid-column: 2 / span 2;
      }
      @media (max-width: 1279px) {
        width: 70.5%;
      }
      @media (max-width: 767px) {
        width: 100%;
      }
      .select2-block {
        width: 248px;
        flex-shrink: 0;
        @media (max-width: 767px) {
          width: 100%;
        }
      }
    }
    @media (max-width: 1600px) {
      grid-column: 3;
    }
    @media (max-width: 767px) {
      padding: 0;
      flex-direction: column-reverse;
      gap: 8px;
    }
    .search {
      width: 100%;
    }
  }
  &__archive {
    height: 50px;
    flex-shrink: 0;
    padding: 0 16px;
    gap: 10px;
    @include body;
    font-weight: 400;
    line-height: 1;
    text-transform: none;
    @media (max-width: 1279px) {
      padding: 0 21px;
    }
    svg {
      width: 16px;
      height: 16px;
      flex-shrink: 0;
      fill: $white;
    }
  }
  &__body {
    margin-bottom: 24px;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    @media (max-width: 1600px) {
      grid-template-columns: repeat(3, 1fr);
    }
    @media (max-width: 1279px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}