.breadcrumbs {
  color: $white;
  &_white {
    color: $base-700;
    .breadcrumbs__item {
      &:not(:last-child) {
        margin-right: 24px;
        &::after {
          background-color: $gold;
        }
      }
      &:last-child {
        color: $base-900;
      }
    }
  }
  @media (max-width: 767px) {
    margin: 0 -16px;
    width: calc(100% + 32px);
  }
  &__list {
    display: flex;
    align-items: center;
    @include label;
    @media (max-width: 767px) {
      overflow-x: auto;
      scrollbar-width: none;
    }
  }
  &__item {
    position: relative;
    @media (max-width: 767px) {
      &:first-child {
        padding-left: 16px;
      }
      &:last-child {
        padding-right: 16px;
      }
    }
    &:not(:last-child) {
      margin-right: 24px;
      &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: calc(100% + 10px);
        transform: translateY(-50%);
        width: 6px;
        height: 6px;
        border-radius: 1px;
        background-color: $white;
        pointer-events: none;
      }
    }
    &:last-child {
      color: $gold;
    }
  }
  &__link {
    display: flex;
    white-space: nowrap;
  }
}