.partners {
  @include section;
  &__heading {
    padding-top: 4px;
    margin-bottom: 20px;
    @media (max-width: 767px) {
      padding-top: 2px;
      margin-bottom: 22px;
    }
  }
  &__title {
    @include h2;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    @media (max-width: 1279px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__item {
    height: 180px;
    display: flex;
    align-items: center;
    justify-content: center;
    @media (max-width: 767px) {
      height: 120px;
    }
    &-img {
      width: 100%;
      max-width: 360px;
      height: 100px;
      img {
        @include contain;
      }
    }
  }
}